Body {
  background-color: #f5a267;
}

.app {
  width: 350px;
  height: 500px;
  position: fixed;
  text-align: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 25px;
  color: #69635f;
  background-color: white;
}

.btns button {
  padding: 1em;
  border: none;
  margin-right: 15px;
  margin-top: 1em;
  border-radius: 10px;
}

.app img {
  width: 120px;
  height: 120px;
  margin-top: 50px;
  border-radius: 50%;
}

i {
  color:blue;
  margin: 5px;
  padding-top: 5px;
}

.likes {
  color: rgb(56, 45, 45);
  margin: 5px;
}